* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.wrapper {
  display: flex;
  overflow: auto;
  scroll-snap-type: x proximity;
}

section {
  height: 95vh;
  min-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  scroll-snap-align: start;
}



.one { 
  background: black; 
}

.two { 
  background: green; 
}

.three { 
  background: yellow; 
}
.four { 
  background: red; 
}
.five { 
  background: white; 
}